body#visual-portal-wrapper.pat-plone #edit-zone .contenttype-frontpage::after {
  content: '';
}

body#visual-portal-wrapper.pat-plone .outer-wrapper .contenttype-frontpage::before {
  content: '';
}

body#visual-portal-wrapper.pat-plone #edit-zone .contenttype-teaser::after {
  content: '';
}

body#visual-portal-wrapper.pat-plone .outer-wrapper .contenttype-teaser::before {
  content: '';
}

/* Teaser parameters */

#frontpage-teaser {
  position: relative;
  margin-bottom: 30px;
}

.navigation {
  position: absolute;
  top: 50%;
  display: none;
  width: 100%;
  margin-top: -35px;
}

.teaser-text {
  position: absolute;
  bottom: 0;
  width: 100%;
  padding: 20px;
  color: rgb(255, 255, 255);
  background-color: rgba(0, 0, 0, .5);
}

.teaser {
  position: absolute;
  width: 100%;
  height: auto;
}

.teaser h2 {
  margin-top: 0;
}

.teaser p {
  margin-bottom: 0;
}

.teaser h2,
.teaser p,
.teaser img {
  width: 100%;
  height: auto;
}

#frontpage-teaser .prev,
#frontpage-teaser .next {
  font-size: 30px;
  position: absolute;
  top: 15%;
  padding: 20px 10px;
  color: rgba(255, 255, 255, .5);
  background-color: rgba(0, 0, 0, .5);
}

#frontpage-teaser .prev {
  left: 0;
}

#frontpage-teaser .next {
  right: 0;
}

#frontpage-teaser .prev:hover,
#frontpage-teaser .next:hover {
  background-color: rgba(0, 0, 0, .8);
}

#frontpage-teaser .pager {
  line-height: 0;
  position: absolute;
  top: 20px;
  width: 100%;
  text-align: center;
}

#frontpage-teaser .pager a {
  display: none;
  width: 50px;
  height: 10px;
  margin-right: 10px;
  background-color: rgba(0, 0, 0, .5);
}

#frontpage-teaser .pager a.selected,
#frontpage-teaser .pager a:hover {
  background-color: rgba(0, 0, 0, .8);
}

#frontpage-teaser .pager a span {
  display: none;
}

/* Frontpage parameters */

.frontpage-text {
  margin-bottom: 30px;
}

.frontpage-news {
  margin: 0 -15px;
}

.news-wrapper {
  height: auto;
  margin-bottom: 30px;
}

.news-image img {
  width: 100%;
  height: 100%;
}

.news-text-wrapper {
  margin: 0 0 0 1px;
}

/* Overrides */

.template-view .formHelp {
  display: none;
}

#content-core #form-widgets-image img {
  width: 100%;
  height: auto;
}

#content-core #form-widgets-image img[alt='JPEG image'] {
  width: 16px;
  height: auto;
}

.row {
  padding: 0 15px;
}

